.selector-page-wrapper {
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100%;
}
.selector-page-wrapper .selector-page-container {
    height: 90vh;
    width: 100%;
}
.selector-page-wrapper .selector-page-header {
    border-bottom: 1px solid #a9b6cb;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    padding: 8px 0 8px 20px;
    position: relative;
    width: 100%;
}
.selector-page-wrapper .selector-page-header .selector-page-header-image {
    height: 40px;
    width: 40px;
}
.selector-page-wrapper .selector-page-header .selector-page-header-image img {
    height: 100%;
    width: 100%;
}
.selector-page-wrapper .selector-page-header > div > span {
    color: #647696;
    font-size: 20px;
    font-weight: bold;
    left: 70px;
    position: absolute;
    top: 18px;
}
.selector-page-wrapper .selector-page-header .cancel-icon {
    margin-right: 30px;
    margin-top: 12px;
    margin-left: auto;
    cursor: pointer;
}
.selector_container {
    margin: 0 auto;
    overflow: hidden;
    height: 92vh;
}
.info-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.modal-page-wrapper {
    width: calc(100vw - 300px) !important;
    height: calc(100vh - 100px) !important;
    border-radius: 10px !important;
}
.modal_selector_container {
    height: calc(100vh - 160px) !important;
    border-bottom-left-radius: 10px !important;
    border-bottom-right-radius: 10px !important;
}
